

.timeline-education
{
    position: relative;
    max-width: 100%;
    margin: 100px auto;
}

.date-education
{
    position: absolute;
    top: -20px;
    left: 0px;
    height: 35px;
    border-radius: 20px;
    color: #fff;
    place-items:  center;
    font-size: 14px;

}
.container-education
{
    padding: 10px 65px;
    position: relative;
    width: 50%;
opacity: 0;
animation: moveup 1s linear forwards;

z-index: 1;
}

@keyframes moveup {
    0%
    {
        opacity: 1;
        transform: translateY(30px);
    }
    100%
    {
    opacity: 1;
        transform: translateY(0px);
    }
    

    
}

.container-education:nth-child(1)
{
    animation-delay: 2s;
}

.container-education:nth-child(2)
{
    animation-delay: 1s;
}



.text-box-education{
    margin-top: '15px';
    padding: 20px 20px;
    background:  white;
    position: relative;
    border-radius: 15px;
    font-size: 15px;
    border-style: solid;
    border-color: white;
    
}

.last-text-box-education
{
    border-color: #6aa4e6;
}
.left-container-education
{
    left:0;
}

.right-container-education
{
    left:50%;
}

.container-education img
{
    position: absolute;
    width: 100px;
    border-radius: 50%;
    right: -50px;
    top: 32px;
    z-index: 1;
    border-style: solid;
    border-color: #6aa4e6;

}

.right-container-education img
{
    left: -50px;
    
}

.timeline-education::after
{
    border-color: #6aa4e6;
    content:'';
    position: absolute;
    width: 3px;
    height: 100%;
    background: #6aa4e6;
    bottom: 0;
    left:50%;
    margin-left: -3px;
    z-index: 0;
    animation:  moveline 2s linear backwards;
}

@keyframes moveline
{
    0%
    {
        height: 0;
    }
    100%
    {
        height: 100%;
    }
}
.text-box-education h4{
    font-weight: 600;
    font-size: 24px;
}

.text-box-education small
{
    
    left:50%;
    margin-bottom: 15px ;
}

.text-box-education p
{
    padding: 3px;
}
.left-container-arrow-education
{
    height: 0;
    width: 0;
    position: absolute;
    top: 55px;
    z-index: 1;
    border-top: 16px solid transparent;
    border-bottom: 16px solid transparent;
    border-left: 16px solid white;
    right: -16px;
}

.last-container-arrow-education
{
    height: 0;
    width: 0;
    position: absolute;
    top: 55px;
    z-index: 1;
    border-top: 16px solid transparent;
    border-bottom: 16px solid transparent;
    border-left: 16px solid #6aa4e6;
    right: -16px;
}

.right-container-arrow-education
{
    height: 0;
    width: 0;
    position: absolute;
    top: 55px;
    z-index: 1;
    border-top: 16px solid transparent;
    border-bottom: 16px solid transparent;
    border-right: 16px solid white;
    left: -16px;
}

@media screen and (max-width: 900px)
{
    .date-education
{
    left: 0px;


}

    .timeline-education
    {
        margin: 50px auto;
    }

    .timeline-education::after
    {
        left: 24px;
    }

    .container-education
    {
        width: 100%;
        padding-left: 60px;
        padding-right: 3px;

    }
    .container-education img
{
    position: absolute;
    width: 50px;
    left: -2px;
    top: 36px;
    z-index: 289;

}




    .right-container-education
    {
        left: 0;
    }

    .left-container-education img, .right-container-education img
    {
        left: 0px;
    }

    .left-container-arrow-education, .right-container-arrow-education
    {   
        top: 30px;
        border-right: 16px solid white;
        border-left: 0;
        left: -16px;

    }

    .last-container-arrow-education
    {
        border-right: 16px solid #6aa4e6;
    }
}